<template>
	<b-container class="step login">
		<b-row class="justify-content-center">
			<b-col md="9">
				<ul class="text-left">
					<li>
						Hng ha ch ng b 1 chiu duy nht t Kiotviet sang Website
						thng qua SKU. Trng hp ngt kt ni gian hng ang ng b
						vi Website  kt ni vi 1 gian hng mi, qu khch vui lng
						"Xa hng ha" ca gian hng c trn Website  m bo thng
						tin hng ha mi c ng b y .
					</li>
					<li style="margin-bottom: 0px;">
						Khi khch hng t hng ti Website, n hng s nhanh chng
						c ng b v KiotViet. Cc n hng ch ng b 1 chiu duy
						nht t Website sang Kiotviet m khng c chiu ngc li. Nu
						n hng c cha hng ha v khch hng cha c trn KiotViet,
						thng tin ca hng ha v khch hng s c thm mi vo
						KiotViet.
					</li>
				</ul>
			</b-col>
		</b-row>

		<div v-show="waitingConfig" style="text-align: center; margin-top: 30px">
			<b-spinner variant="success" label="Spinning" />
		</div>
		<b-row v-show="!waitingConfig" class="justify-content-center">
			<b-col md="5">
				<b-card-group>
					<b-card
						style="max-width: 100%; padding: 0px"
						no-body
						class="p-8"
					>
						<div class="errors" v-if="errorMessage">
							<p v-html="errorMessage"></p>
							<font-awesome-icon
								style="position: absolute; left: 35px; top: 40px; color: #fff"
								icon="exclamation-triangle"
							></font-awesome-icon>
						</div>
						<img
							v-show="!errorMessage"
							class="mt-2 mb-2 logo"
							src="https://cdn-app.kiotviet.vn/retailler/Content/kiotvietLogo.png"
							alt="Fluid image"
						/>
						<p
							class="text-center"
							style="font-size: 14px; font-weight: bold"
						>
							Thng s kt ni
						</p>
						<b-card-body>
							<b-form style="position: relative">
								<b-input-group class="mb-3">
									<input
										autofocus
										type="text"
										class="form-control input-login"
										:class="{ error: errorRetailer }"
										v-model="request.retailer"
										require
										placeholder="a ch truy cp ca hng"
										style="padding-right: 100px"
									/>
									<span class="text-appden">kiotviet.vn</span>
								</b-input-group>
								<b-input-group class="mb-3">
									<input
										type="text"
										class="form-control input-login"
										:class="{ error: errorClientId }"
										v-model="request.client_id"
										require
										placeholder="Client ID"
									/>
								</b-input-group>
								<b-input-group class="mb-3">
									<input
										type="text"
										class="form-control input-login"
										:class="{ error: errorClientSecret }"
										v-model="request.client_secret"
										require
										placeholder="M bo mt"
									/>
								</b-input-group>
								<div class="text-center">
									<b-button
										style="font-size: inherit"
										:disabled="waiting"
										variant="success"
										class="text-center"
										@click="login()"
										@keyup.enter="login()"
										>Tip theo
										<b-spinner v-show="waiting" small />
										<span class="sr-only">Loading...</span>
									</b-button>
								</div>
							</b-form>
						</b-card-body>
					</b-card>
				</b-card-group>
			</b-col>
		</b-row>
	</b-container>
</template>
<script>
import { mapMutations } from "vuex";
import { router } from "../../router";
import { AuthService } from "../../services/AuthService";
import { CategoryService } from "../../services/CategoryService";
import mixins from "../../mixins";

export default {
	name: "ConfigInfoSync",
	mixins: [mixins],
	data() {
		return {
			request: {
				client_id: "",
				client_secret: "",
				retailer: ""
			},
			waiting: false,
			waitingConfig: false,
			errorMessage: "",
			checkLogin: false,
			errorClientId: false,
			errorClientSecret: false,
			errorRetailer: false
		};
	},
	methods: {
		...mapMutations("Auth", ["setCheckLogin"]),
		...mapMutations(["resetProcessBar", "resetProcessBarGetData"]),
		async login() {
			const messageError =
				"Thng tin ng nhp cha ng, vui lng nhp </br> li thng tin hoc lin h 18006162  c h tr!";

			if (!this.request.retailer) {
				this.errorRetailer = true;
				this.errorMessage = messageError;
				return;
			} else {
				this.errorRetailer = false;
			}

			if (!this.request.client_id) {
				this.errorClientId = true;
				this.errorMessage = messageError;
				return;
			} else {
				this.errorClientId = false;
			}

			if (!this.request.client_secret) {
				this.errorClientSecret = true;
				this.errorMessage = messageError;
				return;
			} else {
				this.errorClientSecret = false;
			}

			this.waiting = true;

			const response = await AuthService.getAccessToken(this.request);
			if (response.status == "success") {
				const request = {
					retailer: this.request.retailer
				};
				const category = await CategoryService.getCategoryKiotviet(request);
				if (category.data.data) {
					if (category.data.status == "success") {
						await AuthService.saveConfigRetailer(request);
						await AuthService.registerWebhook();
						this.resetSetting();
						// remove indexDB
						this.waiting = false;

						router.push({
							name: "Branch"
						});
					} else {
						this.waiting = false;
						this.errorMessage = messageError;
						return;
					}
				} else {
					this.waiting = false;
					this.errorMessage = messageError;
				}
			} else {
				this.waiting = false;
				this.errorMessage = messageError;
			}
		},
		async getConfig() {
			const response = await AuthService.getConfig();
			this.waitingConfig = false;
			if (response.status == "success") {
				this.request.client_id = response.data.client_id;
				this.request.client_secret = response.data.client_secret;
				this.request.retailer = response.data.retailer;
			}
		}
	},
	mounted() {
		this.getConfig();
	}
};
</script>



