<template>
	<div>
		<b-form-group label="Chn nhm  ng b">
			<b-form-checkbox-group
				stacked
				v-model="configCategory"
				:options="[
					{ text: 'Xa cc nhm hng hin c trn Website', value: 1 }
				]"
			>
			</b-form-checkbox-group>
			<b-form-checkbox-group
				stacked
				v-model="configCategory"
				@change="changeConfig($event)"
				:options="[
					{
						text:
							'Thm mi nhm hng trn KiotViet cha tn ti trn Website',
						value: 2
					}
				]"
			>
			</b-form-checkbox-group>
		</b-form-group>
	</div>
</template>
<script>
export default {
	name: "CategoryHeader",
	data() {
		return {
			configCategory: []
		};
	},
	watch: {
		// eslint-disable-next-line no-unused-vars
		configCategory(value) {
			this.changeConfig();
		}
	},
	methods: {
		changeConfig() {
			if (this.configCategory.indexOf(2) !== -1) {
				this.$parent.$refs.categoryTable.setDefaultNewData(true);
			} else {
				this.$parent.$refs.categoryTable.setDefaultNewData(false);
			}
		}
	}
};
</script>



