<template>
	<b-container>
		<b-row class="justify-content-center">
			<b-col md="12">
				<div class="step config-info">
					<ul class="text-left description">
						<li>
							Hng ha ch ng b 1 chiu duy nht t Kiotviet sang
							Website thng qua SKU. Trng hp ngt kt ni gian hng
							ang ng b vi Website  kt ni vi 1 gian hng mi,
							qu khch vui lng "Xa hng ha" ca gian hng c trn
							Website  m bo thng tin hng ha mi c ng b y
							.
						</li>
						<li>
							Khi khch hng t hng ti Website, n hng s nhanh
							chng c ng b v KiotViet. Cc n hng ch ng b 1
							chiu duy nht t Website sang Kiotviet m khng c chiu
							ngc li. Nu n hng c cha hng ha v khch hng cha
							c trn KiotViet, thng tin ca hng ha v khch hng s
							c thm mi vo KiotViet.
						</li>
					</ul>
					<div class="info-login">
						<b-col md="12">
							<b-row align-h="center" class="box-info">
								<b-col><span>Gian hng kt ni</span></b-col>
								<b-col>
									<p>{{ retailer }}</p>
									<a
										target="_blank"
										:href="`https://${retailer}.kiotviet.vn`"
										>{{ `https://${retailer}.kiotviet.vn` }}
									</a>
								</b-col>
								<b-col>
									<b-button @click="stopConnection" variant="danger">
										Ngt kt ni
										<img
											class="icon-disconnect"
											src="./../../../assets/img/icon_disconnect.png"
										/>
									</b-button>

									<b-button @click="fastSync()" variant="success">
										ng b nhanh
									</b-button>

									<b-button @click="toBranch()" variant="info"
										>Chn chi nhnh</b-button
									>

									<!--<b-button @click="toWebhook()" variant="warning">
										Webhooks
									</b-button>-->

									<StopConnection
										ref="StopConnection"
									></StopConnection>
								</b-col>
							</b-row>
						</b-col>
					</div>
				</div>
			</b-col>
		</b-row>
	</b-container>
</template>
<script>
import { mapMutations } from "vuex";
import { router } from "../../../router";
import ConfigSetting from "./../ConfigSetting";
import StopConnection from "../connection/StopConnection";
import HelperProduct from "../../../utils/HelperProduct";
import mixins from "../../../mixins";
import { indexDbService } from "../../../services/indexDbService";

export default {
	name: "ConfigInfoSync",
	components: {
		StopConnection
	},
	mixins: [mixins],
	data() {
		return {
			waiting: false
		};
	},
	methods: {
		toBranch() {
			router.push({
				name: "Branch"
			});
		},
		toWebhook() {
			router.push({
				name: "WebHook"
			});
		},
		...mapMutations("Step", ["setLastStep", "setCurrentStep"]),
		...mapMutations("Product", ["setProductUpdate"]),
		...mapMutations(["setFastSync"]),
		stopConnection() {
			this.$refs["StopConnection"].showModal();
		},
		async fastSync() {
			this.setCurrentStep(ConfigSetting.STEPS.length + 1);
			const productUpdate = await this.getIdProductKiotvietUpdate();
			this.setProductUpdate(productUpdate);
			router.push({
				name: "Sync"
			});
		},
		async getIdProductKiotvietUpdate() {
			const productIndexDb = await indexDbService.getAll("products");
			const productKiotviet = HelperProduct.filterProductKiotviet(
				productIndexDb
			);

			const productId = productKiotviet.map(product => {
				return product.id;
			});
			return productId;
		}
	},
	computed: {
		retailer() {
			return this.$parent.retailer;
		}
	}
};
</script>



